import { CButton, CCard, CCardBody, CTable, CTableBody, CTableDataCell, CTableHead, CTableHeaderCell, CTableRow } from '@coreui/react'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify'
import { DataTable } from 'src/components';

const CategoryList = () => {
  const [category, setCategory] = useState([]);

  const handleDelete = (itemId) => { 
    const arconfirm = confirm("Are you sure you want to delete this?")
    if(!arconfirm){
      return;
    }
    var requestOptions = {
      method: 'POST',
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({"_id": itemId}),
      redirect: 'follow'
    };
    fetch(process.env.REACT_APP_API_URL+"category/delete", requestOptions)
      .then(response => response.json())
      .then(result => {       
          toast.success(result.message);        
      })
      .catch(error => {
        console.error(error)
        toast.error(error.message);        
      });

  };


  useEffect(() => {

    var requestOptions = { 
      method: 'POST',
      redirect: 'follow'
    };

    let url =  process.env.REACT_APP_API_URL+"category/list"
    fetch(url, requestOptions)
      .then(response => response.json())
      .then(result => { setCategory(result.data) })
      .catch(error => console.log('error', error));

  }, []);

  const tableHeaders = [
                        { name: 'Image', field: 'image', short: false, type: 'image', width:150},
                        { name: 'Name', field: 'name', short: true, width:250}, 
                        { name: 'Parent', field: 'parent'}, 
                        { name: 'Status', field: 'status', type: 'status'},                         
                        { name: 'Action', field: '_id', short: false, width:100,  type: 'action'}
                      ];

  return (
    <>
      <ToastContainer />
      <div className='d-flex justify-content-between  align-items-center  mb-4'>
        <h1 className='headingType1'>Category Listing</h1>
        <Link to={'/category/add'} className='btn btn-primary'>Add</Link>
      </div>

      <CCard>
        <CCardBody>
          {category && <DataTable data={category} headers={tableHeaders} onDelete={handleDelete}/>}
        </CCardBody>
      </CCard>
    </>
  )
}

export default CategoryList
